export const TRANSLATE_INSERT = Symbol('TRANSLATE_INSERT');

export const TRANSLATE_REPLACE = Symbol('TRANSLATE_REPLACE');

export function insertTranslatedText(text) {
  return {
    type: TRANSLATE_INSERT,
    payload: { text },
  };
}

export function replaceWithTranslatedText(text) {
  return {
    type: TRANSLATE_REPLACE,
    payload: { text },
  };
}